import { Component, OnInit, Input } from '@angular/core';
import { trigger, transition, style, animate } from "@angular/animations";

@Component({
  selector: 'app-carousel',
  templateUrl: './carousel.component.html',
  styleUrls: ['./carousel.component.css'],
  animations: [
    trigger('carouselAnimation', [
      transition('void => *', [
        style({ opacity: 0 }),
        animate('300ms', style({ opacity: 1 }))
      ]),
      transition('* => void', [
        animate('300ms', style({ opacity: 0 }))
      ])
    ])
  ]
})
export class CarouselComponent implements OnInit 
{
  @Input() slides;
  
  txt = [
    "This is test text for src 1",
    "This is test text again for src 2",
    "this is test text another for src 3",
  ];

  currentSlide = 0;
  currentText = 0;

  constructor() { 

  }

  ngOnInit() {
    this.preloadImages();
  }

  preloadImages()
  {
    for (const slide of this.slides)
    {
      new Image().src = slide.src;
    }
  }

  onPreviousClick()
  {
    const previousSlide = this.currentSlide - 1;
    const previousText = this.currentText - 1;
    this.currentSlide = previousSlide < 0 ? this.slides.length - 1: previousSlide;
    this.currentText = previousText < 0 ? this.txt.length - 1: previousText;
    console.log("Previous clicked, new current slide is: ", this.currentSlide);
    console.log("Previous Text Clicked, new Text is: ", this.txt[this.currentText]);
  }

  onNextClick()
  {
    const nextSlide = this.currentSlide + 1;
    const nextText = this.currentText + 1;
    this.currentText = nextText === this.txt.length ? 0 : nextText;
    this.currentSlide = nextSlide === this.slides.length ? 0 : nextSlide;
    console.log("next clicked, new current slide is: ", this.currentSlide);
    console.log("next Text clicked, new current text is: ", this.txt[this.currentText]);
  }

}
