import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomePageModuleComponent } from './home-page-module/home-page-module.component';
import { MarathonPageModuleComponent } from './marathon-page-module/marathon-page-module.component';
import { MyriathonPageModuleComponent } from './myriathon-page-module/myriathon-page-module.component';
import { VideosPageModuleComponent } from './videos-page-module/videos-page-module.component';
import { AboutPageModuleComponent } from './about-page-module/about-page-module.component';


const routes: Routes = [
  {path: '', redirectTo: 'home', pathMatch: 'full'},
  {path: 'home', component: HomePageModuleComponent},
  {path: 'marathons', component: MarathonPageModuleComponent},
  {path: 'rescue', component: MyriathonPageModuleComponent},
  {path: 'myriacast', component: VideosPageModuleComponent},
  {path: 'about', component: AboutPageModuleComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
